/* 	Base
	These base files here so they overwrite the grid
*/
/* 
	Default site settings
	Setting the font-size to 62.5% so we can easily calculate with REM 
	1REM = 10px (relative pixels)
*/
/* body and wrapper on width: 100% and min-height: 100%; */
body, .wrapper {
  width: 100%;
  min-height: 100%; }

html {
  background: #fff;
  font-size: 62.5%;
  color: #000; }

body {
  font-size: 1.6rem;
  line-height: 1.5; }

html, button, input, select, textarea {
  font-family: "Open Sans", Helvetica, Arial;
  font-weight: normal; }

.wrapper {
  position: relative;
  margin: 0 auto;
  width: 100%;
  overflow: hidden; }

/* Max width of images 100% */
img, embed, object {
  max-width: 100%; }

/* Default Links */
a {
  text-decoration: underline;
  color: #1b94bf; }

a:hover {
  color: #12637f;
  text-decoration: none; }

a[href^="tel"], a[href^="sms"], a[href^="fax"] {
  text-decoration: none;
  color: inherit; }

/* 	Default lists and tables have margin-bottom: $margin-default * 2; */
dl, ol, ul, p, pre, table {
  margin-bottom: 24px; }

dd {
  margin-bottom: 12px; }

/* 	OL and UL alignment */
ol, ul {
  padding-left: 24px; }

/* 	Standard UL list-style is disc */
ul {
  list-style-type: disc; }

/*	HEADERS
*	Always try to use a .head class for SEO reasons
*	Only style on h1-h6 when the client uses an editor like TinyMCE and you can't influence the classes
*/
h1,
h2,
h3,
h4,
h5,
h6,
.head,
.head1,
.head2,
.head3,
.head4,
.head5,
.head6 {
  font-family: "Open Sans", Helvetica, Arial;
  margin: 0 0 .8rem;
  line-height: 1.3;
  font-size: 2.4rem; }

h1, .head1 {
  font-size: 3.0rem;
  margin-bottom: .2rem; }

h2, .head2 {
  font-size: 2.6rem;
  margin-bottom: .3rem; }

h3, .head3 {
  font-size: 2.4rem;
  margin-bottom: .2rem; }

h4, .head4 {
  font-size: 2.2rem;
  margin-bottom: .2rem; }

h5, .head5 {
  font-size: 2.0rem;
  margin-bottom: .4rem; }

h6, .head6 {
  font-size: 1.6rem;
  margin-bottom: .6rem; }

/* 	Buttons */
.btn, .input-combined .btn-submit, .btn-theme01, .btn-theme02, .btn-theme03, .btn-theme04, .nav-label, .btn-theme05 {
  display: inline-block;
  padding: 1rem 1.5rem;
  min-height: 4rem;
  /* same as form element height */
  font-family: "Open Sans", Helvetica, Arial;
  line-height: 1.3;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  background-color: #1b94bf;
  border: none;
  /* necessary for type button */
  transition: all .2s ease-out;
  border-radius: 3px; }
  .btn:hover, .input-combined .btn-submit:hover, .btn-theme01:hover, .btn-theme02:hover, .btn-theme03:hover, .btn-theme04:hover, .nav-label:hover, .btn-theme05:hover {
