    margin-bottom: 6px;
    display: block; }

.form-field {
  position: relative;
  max-width: 100%;
  margin-bottom: 24px; }

input[type=email],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=text],
input[type=url],
input[type=date],
select,
textarea {
  width: 100%;
  height: 4.8rem;
  padding: 0 12px;
  background: #fff;
  border: 1px solid #eee;
  line-height: 1;
  transition: 0.1s all ease-in-out;
  -webkit-appearance: none; }
  input[type=email]:hover,
  input[type=number]:hover,
  input[type=password]:hover,
  input[type=search]:hover,
  input[type=tel]:hover,
  input[type=text]:hover,
  input[type=url]:hover,
  input[type=date]:hover,
